import React from 'react'
import styled from 'styled-components'
import logo from '../Images/logo.png'
import {Link} from 'react-router-dom'
import AnchorLink from "react-anchor-link-smooth-scroll";

const Navbar = () => {
  return (
    <DIV id='nav-menu'>
        <div>
            <img src={logo}/>
            <h1>{'</> Anubhav'} </h1>
        </div>
        <div>
            <AnchorLink href='#one' style={{textDecoration:'none'}}><p class="nav-link home">Home</p></AnchorLink>
            <AnchorLink href='#one' style={{textDecoration:'none'}}><p class="nav-link about">About</p></AnchorLink>
            <AnchorLink href='#one' style={{textDecoration:'none'}}><p class="nav-link skills">Skills</p></AnchorLink>
            <AnchorLink href='#one' style={{textDecoration:'none'}}><p class="nav-link projects">Projects</p></AnchorLink>
            <AnchorLink href='#one' style={{textDecoration:'none'}}><p class="nav-link contact">Contact</p></AnchorLink>
            <AnchorLink href='#one'>
                <button class="nav-link resume" onClick={()=>window.location='https://drive.google.com/file/d/1Zp0P8UGwoU05-UBUWMtFxLUxpY2P7_ap/view?usp=sharing'}>Resume</button>
            </AnchorLink>
        </div>
    </DIV>
  )
}

export default Navbar

const DIV=styled.div`
    width:100%;
    height:80px;
    background-color:#000000;
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    *{
        margin: 0;padding:0;
    }
   div{
    /* border: 1px solid white; */
   }
    div:first-child{
        display: flex;
        color:#b5af41;
        font-size: 140%;
        align-items: center;
        /* justify-content: space-between; */
        width: 26.8%;
    }
    div:first-child h1{
       margin: 0 0 0 -11%;
    }

    div img{
        width: 150px;
        height: 95px;
    }

    div:last-child{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 50%;
        font-size: 20px;
    }
    div:last-child p{
        color: #b5af41;
    }
    div:last-child button{
        background-color: #b5af41;
        color: #000000;
        border: none;
        border-radius: 10px;
        padding: 10px 15px;
        font-size: 20px;
    }
    
`